.form-control {
  margin: 0.5rem;
}

.form-control label,
.form-control input,
.form-control textarea {
  display: block;
  margin: 0;
  padding: 0;
}

.form-control label {
  text-align: start;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.form-control input,
.form-control textarea {
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  border-radius: 10px;
  background: #f8f8f8;
  padding: 0.45rem 0.65rem;
  /* margin-bottom: 1rem; */
}

.form-control input:focus,
.form-control textarea:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.form-control--invalid label,
.form-control--invalid p {
  color: red;
}

.form-control--invalid input,
.form-control--invalid textarea {
  border-color: red;
  background: #ffd1d1;
}
