.radio-inputs {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  border-radius: 0.5rem;
  background-color: #eee;
  box-sizing: border-box;
  box-shadow: 0 0 0px 1px rgba(0, 0, 0, 0.06);
  padding: 1rem;
  width: 445px;
  margin: 2rem 0.05rem;
  font-size: 14px;
}

.radio-inputs .radio {
  flex: 1 1 auto;
  text-align: center;
}

.radio-inputs .radio input {
  display: none;
}

.radio-inputs__title {
  padding: 0.5rem;
  font-weight: 800;
  font-size: large;
}

.radio-inputs .radio input:checked + .radio-span {
  font-weight: 600;
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  border: none;
  padding: 0.5rem 0;
  color: rgba(51, 65, 85, 1);
  transition: all 0.15s ease-in-out;
}

.radio-inputs .radio input:checked + .low {
  background-color: #4dff4d;
}

.radio-inputs .radio input:checked + .medium {
  background-color: #ffcc00;
}

.radio-inputs .radio input:checked + .high {
  background-color: #ff4d4d;
}

.radio-inputs .radio input:checked + .admin {
  background-color: #ffdd99;
}

.radio-inputs .radio input:checked + .member {
  background-color: #445dff;
}
